    var ispublic="";
    var isreward="";

    function getDESUtilID(){
            var retid="";
            $.ajax({
                url : "/lnfwzl/khzs/vue/surveys/getDESUtil.action",
                type : "GET",
                data : {
                    "wjid" : getQueryVariable("id"),
                    "isen" : '2',
                },
                async:false,
                success : function(unid) {
                    //return unid;
                    retid = unid;
                },
                error:function(retdata){
                }
            });
            return retid;
    }


