                var status = result.status;
                if (status != '草稿') {
                    $("button[mysubmit=mysubmit]").css("display","");
                }
            });



        },
         mounted(){
            /*//$("button[mysubmit=mysubmit]").css("display","");
            var wjid = getQueryVariable("id");
            //获取当前问卷状态
            $.post("/lnfwzl/khzs/survey/ajaxGetDywj.action?khzsQuestionnaire.id=" + wjid).done(function(result) {
                var status = result.status;
                if (status != '草稿') {
                    $("button[mysubmit=mysubmit]").css("display","");
                }
            });*/


            //设置标题和副标题过长问题
            var textAreas = document.querySelectorAll('textarea[data-adaptheight]');
            // 迭代本页所有的文本域
            for (var i = 0, l = textAreas.length; i < l; i++) {
                var el = textAreas[i];
                el.style.boxSizing = el.style.mozBoxSizing = 'border-box';
                el.style.overflowY = 'hidden';
                var minHeight = el.scrollHeight;
                el.addEventListener('input', function() {
                    adjustHeight(this, minHeight);
                });
                window.addEventListener('resize', function() {
                    adjustHeight(this, minHeight);
                });
                adjustHeight(el, minHeight);
            }


         },
