                    for(var i =0;(retArray!=undefined&&i<retArray.length);i++){
                        var number=0;
                        for(var o=0;o<this.hideAllArray.length;o++){
                            if(this.hideAllArray[o]==retArray[i]){
                                number++;
                            }
                        }
                        if(number==0){
                            $("div[id="+(retArray[i]-1)+"]").css("display","none");//隐藏题目
